.row_poster {
    width: 100%;
    max-height: 100px;
    object-fit: contain;
    transition: transform 450ms;
    margin-right: 10px;
}
.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar {
    display: none; /* Hide scrollbar for webkit browsers */
}
.row_poster:hover {
    transform: scale(1.08);
  /*  opacity: 1; */
}
.row_posterLarge {
    max-height: 250px;
}
.row_posterLarge:hover {
    transform: scale(1.1);
}
.row{
    color: white;
    margin-left: 20px;
}